import React, { Component, useEffect } from "react";
import { useSelector, useDispatch, connect } from "react-redux";
import axios from "axios";
import { HashRouter, Route, Switch } from "react-router-dom";
import { STATUS } from "apiServices/action";
import { throwErrorStatus } from "./services";
import "./scss/style.scss";

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
);

// Containers
const TheLayout = React.lazy(() => import("./containers/TheLayout"));

// Pages
const Page500 = React.lazy(() => import("Page/ErrorPage/ServerError"));
const Authenticate = React.lazy(() => import("Page/Authenticate"));

const ProtectedRoute = ({ props }) => {
  const user = useSelector((state) => state.user);
  const status = useSelector((state) => state.status);
  let thereAreRequest = 0;
  const dispatch = useDispatch();

  useEffect(() => {
    if (user) {
      axios.interceptors.response.use(
        (response) => {
          thereAreRequest--;
          dispatch({
            type: STATUS,
            status: 200,
            statusMessage: null,
            thereAreRequest,
          });
          return response;
        },
        (error) => {
          thereAreRequest = 0;
          const errorStatus = throwErrorStatus(error);
          console.log(status);
          dispatch({ type: STATUS, status: errorStatus, isLoading: false, thereAreRequest });

          setTimeout(() => {
            errorStatus.code = 200
            dispatch({ type: STATUS, status: errorStatus });
          }, 500);
          return Promise.reject(error);
        }
      );

      axios.interceptors.request.use(
        (request) => {
          thereAreRequest++;
          dispatch({ type: STATUS, thereAreRequest });
          return request;
        },
        (error) => {
          thereAreRequest = 0;
          const statusError = throwErrorStatus(error);
          dispatch({ type: STATUS, statusError, isLoading: false, thereAreRequest });
        }
      );
    }
  }, []);
  const renderRoute = (props) => {
    if (status?.code === 401 || !user)
      window.location.href = window.config.REACT_APP_LOGIN;
    return <TheLayout {...props} />;
  };
  return <Route {...props.others} render={renderRoute} />;
};

class App extends Component {
  constructor(props) {
    super(props);
  }

  async componentDidMount() {
    // if (caches && JSON.stringify(caches).length > 2) {
    //   const names = await caches.keys();
    //   await Promise.all(names.map(name => caches.delete(name)));
    //   window.location.reload();
    // }
  }
  render() {
    if (this.props.user) {
      const { sessionId } = this.props.user;
      axios.defaults.headers.common["session-id"] = sessionId;
    }

    axios.defaults.baseURL = window.config.REACT_APP_API_URL;
    axios.defaults.headers.common["Content-Type"] = "application/json";
    axios.defaults.headers.common["Accept"] = "application/json";
    axios.defaults.timeout = 300000;
    axios.defaults.withCredentials = false;
    return (
      <HashRouter>
        <React.Suspense fallback={loading}>
          <Switch>
            <Route
              path="/authenticate"
              name="Landing Page"
              render={(props) => <Authenticate {...props} />}
            />
            <Route
              path="/server-error"
              name="Server error"
              render={(props) => <Page500 {...props} />}
            />
            <ProtectedRoute
              path="/"
              name="Page404"
              component={TheLayout}
              props={this.props}
            />
          </Switch>
        </React.Suspense>
      </HashRouter>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.user,
  };
};

export default connect(mapStateToProps)(App);
